$item-style-color: #00acdc;
$main-background: #fff;
$main-font: "roboto", sans-serif;
$main-font-color: #000;

@mixin flex-space-between {
  display: flex;
  justify-content: space-between;
}

@mixin grid-four-columns {
  display: grid;
  grid-template-columns: repeat(4, 200px);
  grid-gap: 50px 48px;
}
